'use strict';

// MODULES //

import Float64Array from './../../float64';
import Float32Array from './../../float32';
import Int16Array from './../../int16';
import Int32Array from './../../int32';
import Int8Array from './../../int8';
import Uint16Array from './../../uint16';
import Uint32Array from './../../uint32';
import Uint8Array from './../../uint8';
import Uint8ClampedArray from './../../uint8c';
import Complex64Array from './../../complex64';
import Complex128Array from './../../complex128';


// MAIN //

var ctors = {
	'Float64Array': Float64Array,
	'Float32Array': Float32Array,
	'Int32Array': Int32Array,
	'Uint32Array': Uint32Array,
	'Int16Array': Int16Array,
	'Uint16Array': Uint16Array,
	'Int8Array': Int8Array,
	'Uint8Array': Uint8Array,
	'Uint8ClampedArray': Uint8ClampedArray,
	'Complex64Array': Complex64Array,
	'Complex128Array': Complex128Array
};


// EXPORTS //

export default ctors;
